.popup{
	z-index: 10;
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.8);
	top: 0;
	left: 0;
	overflow-y: auto;
	overflow-x: hidden;
	opacity: 0;
	visibility: hidden;
	transition: all 0.8s ease 0s;
	&:target{
		opacity: 1;
		visibility: visible;
		& .popup__content{
			transform: translate(0px,0px);
			opacity: 1;
		}
	}
	&__body{
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 30px 10px;
		transition: all 0.8s ease 0s;
	}
	&__content{
		display: flex;
		flex-direction: column;
		color: black;
		box-shadow: 0px 0px 10px white;
		position: relative;
		transition: all 0.8s ease 0s;
		opacity: 0; 
		transform: translate(0px,-100%);
		border-radius: 10px;
	}
	&__close{
		content: '';
		width: 30px;
		height: 30px;
		position: absolute;
		right: 0px;
		top: 10px;
		&:before{
			content: '';
			background-color: white;
			position: absolute;
			right: 10px;
			top: 15px;
			height: 3px;
			transform: rotate(45deg);
			width: 25px;
		}
		&:after{
			content: '';
			background-color: white;
			position: absolute;
			right: 10px;
			top: 15px;
			height: 3px;
			transform: rotate(-45deg);
			width: 25px;
		}
	}
	&__title{
		color: white;
		background-color: $red1;
		max-width: 800px;
		border-radius: 10px 10px 0 0 ;
		font-size: 24px;
		padding: 0px 0px 1rem 0px;
		padding: 20px 20px 15px 20px ;
		text-align: center;
		display: flex;
		flex-direction: column;
		& span{
			margin-top: 2px;
			font-size: 16px;
		}
	}
	&__wrapper{
		background-color: white;
		flex-grow: 1;
		border-radius: 0 0 10px 10px;
		padding: 0 10px;
		display: flex;
		justify-content: center;
	}
	&__area{
		position: absolute;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
	}
	&__text{
		display: flex;
		padding: 10px;
		& .forma{
			display: flex;
			flex-direction: column;
			&>input{
				margin: 5px;
				padding: 10px; 
				border: 3px solid $red1; 
				border-radius: 10px; 
				margin: 10px;
				&:required:invalid {
					border:2px solid $red1;
				}
			}
		}
	}
	}
	.btn_submit{
		color: white;
		background-color: $red1;
		transition: 0.4s;
		&:hover{
			box-shadow: 0px 0px 10px red;
			background-color: $red2;
		}

	}
	.sended{
		visibility: visible;
		text-align: center;
		transition: opacity 0.4s;
		color: green;
		opacity: 0;
		&.active{
			opacity: 1;
		}
	}